(function() {
  angular.module('akuerapp').directive('geoNav', function() {
    var directive;
    return directive = {
      restrict: 'E',
      templateUrl: 'app/components/geonav/geonav.html',
      controller: "GeoNavController",
      bindToController: true
    };
  }).directive('googlePlaces', function() {
    var directive;
    return directive = {
      restrict: 'E',
      replace: true,
      templateUrl: "app/components/geonav/googleplaces.html",
      link: function($scope, elm, attrs) {
        var autocomplete;
        autocomplete = new google.maps.places.Autocomplete($("#google_places_ac")[0], {});
        return google.maps.event.addListener(autocomplete, 'place_changed', function() {
          var lat, lon, place;
          console.log("place_changed");
          place = autocomplete.getPlace();
          $scope.location = place.geometry.location.lat() + ',' + place.geometry.location.lng();
          lat = place.geometry.location.lat();
          lon = place.geometry.location.lng();
          $scope.search(lat, lon, $scope.distanciaSlider);
          return $scope.$apply();
        });
      }
    };
  });

}).call(this);



(function() {
  angular.module('akuerapp').controller('GeoNavController', function($rootScope, $scope, $timeout, webDevTec, toastr, Establecimiento, uiGmapGoogleMapApi, uiGmapIsReady, MapData) {
    'ngInject';
    $scope.distanciaSlider = MapData.distanciaSlider;
    $scope.$on("slideEnded", function() {
      $scope.circle.radius = $scope.distanciaSlider * 1000;
      $scope.circle.center = $scope.map.center;
      $scope.search($scope.map.center.latitude, $scope.map.center.longitude, $scope.distanciaSlider);
      $scope.windowOptions = {
        visible: false
      };
      $scope.onClick = function() {
        return $scope.windowOptions.visible = !$scope.windowOptions.visible;
      };
      return $scope.closeClick = function() {
        return $scope.windowOptions.visible = false;
      };
    });
    uiGmapGoogleMapApi.then(function(maps) {
      $scope.map = MapData.map;
      $scope.circle = MapData.circle;
      return console.log($scope.circle);
    });
    $scope.location = '';
    return $scope.search = function(lat, lon, distance) {
      distance = distance * 1000;
      Establecimiento.$get('/api/establecimientos', {
        "lat": lat,
        "lon": lon,
        "distancia": distance
      }).then(function(establecimientos) {
        $scope.establecimientos = establecimientos;
        return uiGmapGoogleMapApi.then(function(maps) {
          var e, i, j, latLng, latLonBounds, len, len1, m, map, ref;
          map = $scope.map.control.getGMap();
          map.panTo(new maps.LatLng(lat, lon));
          $scope.map.markers = [];
          MapData.map.markers = [];
          for (i = 0, len = establecimientos.length; i < len; i++) {
            e = establecimientos[i];
            m = {
              id: $scope.map.markers.length + 1,
              latitude: e["latitud"],
              longitude: e["longitud"],
              options: {
                draggable: false
              }
            };
            $scope.map.markers.push(m);
          }
          map = $scope.map.control.getGMap();
          latLonBounds = new maps.LatLngBounds();
          ref = $scope.map.markers;
          for (j = 0, len1 = ref.length; j < len1; j++) {
            e = ref[j];
            lat = e["latitude"];
            lon = e["longitude"];
            if (lat && lon) {
              latLng = new maps.LatLng(lat, lon);
              latLonBounds.extend(latLng);
            }
          }
          if ($scope.map.markers.length > 0) {
            map.setCenter(latLonBounds.getCenter());
            return map.fitBounds(latLonBounds);
          }
        });
      });
    };
  });

}).call(this);



